import { Component, OnInit } from '@angular/core';
import { Platform } from '@ionic/angular';
import { MesasService } from '../services/mesas/mesas.service';
import { StorageService } from '../services/storage-service.service';
import { FundoService } from '../services/utils/fundo.service';

@Component({
  selector: 'app-contmesa',
  templateUrl: './contmesa.page.html',
  styleUrls: ['./contmesa.page.scss'],
})
export class ContmesaPage implements OnInit {

  idMesa = '';

  img = '';

  constructor(private mesaService: MesasService, private storageService: StorageService,
    private plataform: Platform, private fundoService: FundoService) {

  }

  async ngOnInit() {
    const { idMesa } = this.storageService.getSelectedMesa();
    this.idMesa = idMesa;

    await this.plataform.ready()
      .then(() => {
        if (this.plataform.width() <= 700)
          this.fundoService.setCurrentPlataform('mobile');
        else
          this.fundoService.setCurrentPlataform('pc');

        this.img = this.fundoService.changeImg();
      });
  }

}
